.themeSwitcher{
    font-size: .7rem;
    display: flex;
    
    align-items: center;
    gap: 20px;
}
.switchElement {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }
  
  /* Hide default HTML checkbox */
  .switchElement input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    border-radius: 34px;
  outline: none;
    bottom: 0;
    background: linear-gradient(45deg,hsl(210, 78%, 56%) , hsl(146, 68%, 55%)) ;;
    -webkit-transition: .4s;
    transition: .4s;
  }

  input:checked + .slider{
    background: hsl(230, 22%, 74%) ;;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
  }

 
  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
  }
  
  .slider:before {
    border-radius: 50%;
  }
  

  @media (max-width:768px) {
    .themeSwitcher{
      justify-content: space-between;
    }
    
  }